
body{
  background-color: #272C34;
}
.home-header {
  background-color: #cccccc;
  color: #272C34;
  text-align: center;
  width: 100%;
  font-size: 70px;
  min-height: 300px;
  padding-top: 50px;
  margin-top: 10%;
}
.chevron {
  text-align: center;
  font-size: 100px;
  color: #cccccc;
}
.adc:hover {
  color:#242322;
  cursor: pointer;
}
.myFace {
  max-width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  box-shadow: 5px 10px #4f555e;
}
.aboutBio {
  text-align: justify;
  color: #FFFFFF;
  padding-top: 10px;
  font-family: roboto;
  font-size: large;
}
.aboutPadding {
  padding-right: 10px;
  padding-right: 10px;
}
hr {
  width: 40%;
}
.divider {
  background-color: white;
}

.about {
  padding-top: 25px;
  padding-bottom: 25px;
}

.resume {
  color: #FFFFFF;
  padding-top: 25px;
  padding-bottom: 25px;
  font-family: roboto;
  font-size: large;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.projects {
  color: #FFFFFF;
  font-family: roboto;
  padding-top: 25px;
  padding-bottom: 25px;
  max-height: 350px;
  min-height: 350px;

}

.projectImage {
  max-width: 50%;
  max-height: 300px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 10px 10px #4f555e;
}
.projectParent {
  position: relative;
}
.projectChild {
  background-color: rgba(96, 125, 139, 0.6);
  position: sticky;
  right: 10px;
  top:50px;
  
}
.center {
  justify-content: center;
}

.experience {
  padding-top: 25px;
  padding-bottom: 25px;
  justify-content: center;
}

.card {
  width: 300px;
  height: 400px;
  padding: 10px;
}

.portIcon {
  color:  #4f555e;;
  padding: 11px;

}
.portIcon:hover {
  color:#242322;
  cursor: pointer;
}
.main {
  color: #FFFFFF;
  padding-top: 25px;;
}
.iconAlign {
  display: block;
  align-items: right;
}
.hover:hover{
  cursor: pointer;
}
.padding {
  padding: 8px;
}
.nav {
  width: 100%;
}
.hover {
  padding-left: 10px;
}

.drawer {
  color: red;
}

a {
  text-decoration: none;
  color: white;
}

.hamburger
{
  color: white;
}
.menu 
{
  background-color: red;
}
.contact {
  padding: 25px;
}

.contactButton {
  padding-top:20px;
}